import React from "react";
import Loader from "../Loader";
import NoResults from "../NoResults";

export default (props) => (
  props.commands.length
  ? <>
      { props.noResults ? <NoResults /> : <></> }
      <table id="commandsTable" cellSpacing="0" cellPadding="0" width="100%" css={{
        display: props.noResults ? "none" : "block",
        padding: 20,
        borderCollapse: "collapse",
        borderRadius: "5px",
        backgroundColor: "#0C0E11",
        background: "linear-gradient(45deg, #23272A, #0C0E11)",
        fontSize: ".9em",
        textAlign: "left",
        boxShadow: "0 5px 12px 0 rgba(0, 0, 0, .2)",
        overflowX: "auto",
      }}>
        <thead>
          <tr>
            <td css={{
              width: "30%",
              padding: 10,
              fontWeight: "bold",
              color: "#40C4FB",
            }}>
              COMMANDS
            </td>
            <td css={{
              width: "70%",
              padding: 10,
              fontWeight: "bold",
              color: "#3fbefb",
            }}>
              DESCRIPTION
            </td>
          </tr>
        </thead>
        <tbody>
          {
            props.commands.map((command, i) => (
              <tr className="command" key={ i }>
                <td css={{ padding: 10, }}>
                  <div>
                    <code css={{
                      borderBottomLeftRadius: 0,
                      borderBottomRightRadius: 0,
                    }}>
                      { command.name }
                    </code>
                  </div>
                  <div
                    title={ `This command belongs to the ${command.module.toUpperCase()} module` }
                    css={{
                    marginTop: 7,
                  }}>
                    <code css={{
                      backgroundColor: "#40C4FB",
                      background: "linear-gradient(45deg, #40C4FB, #62D9FB)",
                      borderColor: "#3fbefb",
                      borderTopLeftRadius: 0,
                      borderTopRightRadius: 0,
                      color: "#070A0C",
                      fontSize: ".8em",
                      textTransform: "uppercase",
                    }}>{ command.module }</code>
                  </div>
                </td>
                <td css={{ padding: 10, }}>
                  <div>{ command.description }</div>
                </td>
              </tr>
            ))
          }
        </tbody>
      </table>
    </>
  : <Loader />
);
