export default () => {
  return new Promise((resolve, reject) => {
    let commands = [];

    window.fetch("https://api.bastionbot.org/info/commands")
    .then(res => res.json())
    .then(data => {
      for (let command of Object.keys(data)) {
        commands.push({
          name: command,
          description: data[command].description,
          module: data[command].module,
        });
      }

      resolve(commands);
    })
    .catch(e => reject(e));
  });
}
